import React, {useEffect} from "react";
import 'bootstrap/dist/css/bootstrap.min.css';
import { Container } from 'react-bootstrap';
import { Link } from "gatsby";
import { Helmet } from "react-helmet";
import { Image } from "react-bootstrap";
import { Nav } from "react-bootstrap";
import { Navbar } from "react-bootstrap";
import { Collapse } from "react-bootstrap";
import { Row, Col, Button } from "react-bootstrap";

import latestnewsletter from "../../static/newsletter2(1).png";
import firstimg from "../../static/Mrs. Rajamani Teacher Photo 2019-2020.png";
import secondimg from "../../static/2020 Portrait.jpg";
import eventimg from "../../static/DDTVcontest.png";

var latestLink = "https://www.canva.com/design/DAEB5NGHJ5Q/f8YFMnbBYUdyZROJJxH4Ng/view?utm_content=DAEB5NGHJ5Q&utm_campaign=designshare&utm_medium=link&utm_source=sharebutton"
var firstLink = "https://www.canva.com/design/DAEDHBfSKfc/TZ7XXdSnt8WFeM0SMWKkcQ/view?utm_content=DAEDHBfSKfc&utm_campaign=designshare&utm_medium=link&utm_source=sharebutton"
var secondLink = "https://www.canva.com/design/DAEDY5RYeNA/U73XzEhDUGpMXMmUGJR-Vg/view?utm_content=DAEDY5RYeNA&utm_campaign=designshare&utm_medium=link&utm_source=sharebutton"
var eventLink = "/videos01"
var newsTitle = "Learning From Afar"
var newsDesc = "A dive into the impact of distance learning on the education system"


export default function Home() {
  useEffect(() => {
    checkCookie();
    window.onscroll = function() {
      var navlinks = document.getElementsByClassName('navlink');
      if(window.pageYOffset >= 75) {
        document.getElementById('navbar').style.backgroundColor = "rgba(40,50,70,255)";
        for (var i=0;i<navlinks.length;i++) {
          navlinks[i].classList.add("thisHover");
          navlinks[i].classList.remove("hovering");
        }
      }
      else {
        document.getElementById('navbar').style.backgroundColor = "rgba(0.3,0.3,0.3,0.5)";
        for (var p=0;p<navlinks.length;p++) {
          navlinks[p].classList.add("hovering");
          navlinks[p].classList.remove("thisHover");
        }
      }
    };
  }, []);

  function setCookie(cname, cvalue, exdays) {
    var d = new Date();
    d.setTime(d.getTime() + (exdays * 24 * 60 * 60 * 1000));
    var expires = "expires="+d.toUTCString();
    document.cookie = cname + "=" + cvalue + ";" + expires + ";path=/";
  }
  
  function getCookie(cname) {
    var name = cname + "=";
    var ca = document.cookie.split(';');
    for(var i = 0; i < ca.length; i++) {
      var c = ca[i];
      while (c.charAt(0) == ' ') {
        c = c.substring(1);
      }
      if (c.indexOf(name) == 0) {
        return c.substring(name.length, c.length);
      }
    }
    return "";
  }
  
  function checkCookie() {
    var alrVis = getCookie("visited");
    if (alrVis != "true") {
      reveal();
      setCookie("visited", "true", 1);
    }
  }


  function openLink(e) {
    window.open(e);
  }

  function reveal() {
    document.getElementById("popup").classList.add("showy")
    document.getElementById("popup").classList.remove("hide");
    document.getElementById("cover").classList.add("showy");
    document.getElementById("cover").classList.remove("hide");
  }

  function hide() {
    document.getElementById("popup").classList.add("hide")
    document.getElementById("popup").classList.remove("showy");
    document.getElementById("cover").classList.add("hide");
    document.getElementById("cover").classList.remove("showy");
  }

  return (
  <Container fluid>
    <Helmet>
        <meta name="viewport" content="width=device-width, initial-scale=1.0, maximum-scale=1.0, minimum-scale=1.0, 
        height=device-height, target-densitydpi=device-dpi, shrink-to-fit=no, user-scalable=no" />
        <title>BAYouth</title>
        <body id="home-page" />
    </Helmet>
    <Navbar collapseOnSelect expand="lg" fixed="top" id="navbar" style={{ backgroundColor: `rgba(0.3,0.3,0.3,0.5)`, zIndex: 100 }}>
        <Navbar.Brand href="/" style={{ paddingRight: `1.5rem`, color: `white` }}>BAYouth</Navbar.Brand>
        <Navbar.Toggle aria-controls="responsive-navbar-nav" />
        <Navbar.Collapse id="responsive-navbar-nav">
            <Nav className="mr-auto">
                <Nav.Link href="/articles" className = "hovering navlink" style={{ paddingRight: `1.5rem`, paddingLeft: '1.5rem', color: `rgba(200,200,225,200)` }}>Our Articles</Nav.Link>
                <Nav.Link href="/events" className = "hovering navlink" style={{ paddingRight: `1.5rem`, paddingLeft: '1.5rem', color: `rgba(200,200,225,200)` }}>Events</Nav.Link>
            </Nav>
            <Nav>
                <Nav.Link href="/meetus" className = "hovering navlink" style={{ paddingRight: `1.5rem`, paddingLeft: '1.5rem', color: `rgba(200,200,225,200)` }}>Meet the Team</Nav.Link>
            </Nav>
        </Navbar.Collapse>
    </Navbar>

    <div id="cover" className="hide" onClick={hide}></div>

    <div style={{ height: `100%`, width: `100%`, justifyItems: `center`, placeItems: `center`, overflowY: `visible` }}>
      {/* <div id="popup" className="hide center"> */}
        <div style={{ height: `initial` }} id="popup" className="hide center">
          <h4 id="subHead" style={{ margin: `15px 0 0 0`, padding: `0 15px 0 15px`, fontSize: `xx-large` }}><b>SUBSCRIBE FOR OUR LATEST UPDATES</b></h4>
          <h4 id="subHead2" style={{ margin: `15px 0 0 0`, padding: `0 15px 0 15px`, fontSize: `xx-large` }}><b>SUBSCRIBE</b></h4>
          <h3 id="x" onClick={hide} style={{ position: `absolute`, top: `0.5vh`, right: `1.25vw` , fontFamily: `Playfair Display` }}>X</h3>
          <p id="subText" style={{ padding: `15px 15px 0 15px`, margin: 0, fontSize: `x-large` }}>Subscribe to receive a notification whenever we release a new issue, all for free!</p>
          <div style={{ height: `15px` }}></div>
          <form autoComplete="off" className="form" action="https://formspree.io/mzbjnzbw" method="POST" style={{ margin: 0 }}>
            <div className="contact-form" id="inputEmail">
              <input type="text" name="email" className="input" placeholder="Email Address" style={{ width: `70%`, border: `2px solid gray`, marginTop: `15px` }} required/>
              <br></br>
              <Button type="submit" style={{ marginTop: `15px`, width: `70%`, marginBottom: `15px` }} onClick={hide}>SUBSCRIBE NOW</Button>
            </div>
          </form>
        </div>
      {/* </div> */}
    </div>

    <div id="home-body">
      <Row>
          <Col xs={12} sm={12} md={12} lg={12} xl={12} className="center overlay" style={{ padding: 0, margin: `30vh 0` }}>
              <div id="homeOverlay">
                  <h1 id="p1">Bay Area Youth Initiative</h1>
                  <p id="p2"><b>Bay Area Youth Initiative</b> (BAYouth) is an organization of Bay Area teenagers who are 
                  dedicated to giving back to the community. Our mission is to express the youth voice, and provide meaningful and impactful events to
                  the Bay Area.</p>
              </div>
          </Col>
      </Row>
    </div>

    <div className="headerBack">
      <Row style={{ width: `100%`, placeItems: `center` }}>
        <Col className="headerCol" xs={12} sm={12} md={12} lg={12} xl={12} style={{ width: `100%`, placeItems: `center`, marginLeft: `15px` }}>
          <p className="ourArticles oa1">Articles</p>
          <a className="ourArticles oa2 hoverUnder" href="/articles">See More {`>`}</a>
        </Col>
      </Row>
    </div>

    <Row style={{ margin: 0, padding: 0 }}>
        <Col xs={12} sm={12} md={6} lg={6} xl={6} className="newsCol" style={{ padding: 0, margin: 0, position: `relative` }}>
            <div className="imgdivid imgdiv imgborder" onClick={() => openLink(latestLink)}>
                <Image className="displayImg" src={latestnewsletter} rounded fluid></Image>
                <div className ="openingInfo overlay" style={{ margin: 0, verticalAlign: `middle` }}>
                  <p><strong>{newsTitle}</strong><br></br>{newsDesc}</p>
                </div>
            </div>
        </Col>
        <Col className="descCol" xs={12} sm={12} md={6} lg={6} xl={6} style={{ padding: 0, margin: 0 }}>
          <Col xs={12} sm={12} md={3} lg={3} xl={3} className="showcase showCol1" style={{ padding: 0, margin: `15px 0 0 0` }}>
            <div className="imgdiv imgborder showcase1" onClick={() => openLink(firstLink)}>
              <div className="imgdiv showcaseimg" id="img1">
                {/* <Image className="image" src={firstimg} fluid/> */}
              </div>
              <div className="desDiv">
                <div>
                  <h3 className="desc">Teaching Through Turmoil</h3>
                  <p className="desc"><i>by Jason Shan</i></p>
                  <p className="descText">Matangi Rajamani is an 8th grade science and math teacher at Kennedy Middle School. 
                    In the article, Ms. Rajamani gives insight into her strategies for teaching and helping 
                    each individual student and her experience of shelter-in-place.</p>
                </div>
              </div>
            </div>
            <div className="firstArticle overlay hoverGray" style={{ margin: 0 }} onClick={() => openLink(firstLink)}></div>
          </Col>
          <Col xs={12} sm={12} md={3} lg={3} xl={3} className="descCol" style={{ margin: 0, padding: 0 }}>
                <Col xs={12} sm={12} md={3} lg={3} xl={3} className="showCol2 showcase" style={{ margin: 0, padding: 0 }}>
                    <div className="imgdiv imgborder showcase2" style={{ marginRight: `-15px` }} onClick={() => openLink(secondLink)}>
                      <div className="imgdiv showcaseimg" id="img2">
                          {/* <Image className="image" src={secondimg} fluid/> */}
                      </div>
                      <div className="desDiv">
                          <div>
                          <h3 className="desc">Behind the Scenes</h3>
                          <p className="desc"><i>by Belinda Chen</i></p>
                          <p className="descText">Board Members Jeff Moe and Rosa Kim reveal the 
                          rationale leading up to the change in grading policies and possible plans 
                          for the upcoming school year. Both Mr. Moe and Ms. Kim encourage 
                          students to use this free time to improve upon themselves for their futures.</p>
                          </div>
                      </div>
                    </div>
                    <div className="secondArticle overlay hoverGray" style={{ margin: 0 }} onClick={() => openLink(secondLink)}></div>
                </Col>
            </Col>
        </Col>
    </Row>

    <div className="headerBack">
      <Row style={{ width: `100%`, placeItems: `center` }}>
        <Col className="headerCol" xs={12} sm={12} md={12} lg={12} xl={12} style={{ width: `100%`, placeItems: `center`, marginLeft: `15px` }}>
          <p className="ourArticles oa1">Events</p>
          <a className="ourArticles oa2 hoverUnder" href="/events">See More {`>`}</a>
        </Col>
      </Row>
    </div>

    <div className="imgborder back" style={{ marginTop: `15px` }}>
      <Row id="eventRow" onClick={() => openLink(eventLink)} style={{ width: `100%`, placeItems: `center`, position: `relative` }}>
        <Col className="headerCol" xs={12} sm={12} md={12} lg={12} xl={12} style={{ width: `100%`, placeItems: `center`, marginLeft: `15px` }}>
          <h3 id="eventTitle" style={{ margin: `0.5rem` }}>Video and Essay Competition Winners Interviews</h3>
        </Col>
        <Col className="headerCol" xs={12} sm={12} md={12} lg={12} xl={12} style={{ width: `100%`, placeItems: `center`, marginLeft: `15px`, marginBottom: `15px`, position: `relative` }}>
          <Image src={eventimg} className="displayImg" id="imgForEvent" rounded fluid></Image>
        </Col>
        <div id="event" className="overlay hoverGray" style={{ margin: `0 12px 12px 0`, borderRadius: `5px`, height: `calc(100% + 6px)`, width: `calc(100% + 6px)`, top: `-3px`, left: `12px` }}></div>
      </Row>
    </div>


    <div className="headerBack">
      <Row style={{ width: `100%`, placeItems: `center` }}>
        <Col className="headerCol" xs={12} sm={12} md={12} lg={12} xl={12} style={{ width: `100%`, placeItems: `center`, marginLeft: `15px` }}>
          <p className="ourArticles oa1">Contact Us</p>
        </Col>
      </Row>
    </div>

    <Row className="bottom" id="tohere" >
      <Col className="d-flex align-items-center justify-content-center back" style={{ margin: 0, padding: 0 }}>
          <form autoComplete="off" className="form" action="https://formspree.io/mzbjlnbq" method="POST">
              <div style={{ paddingTop: `1em`, paddingBottom: `1em` }}>
                  <div className="wrapper">
                      <div className="title">
                          <h1>Contact Us</h1>
                      </div>
                      <div className="contact-form">
                          <div className="input-fields">
                              <input type="text" name="name" className="input" placeholder="Name" required/>
                              <input type="text" name="email" className="input" placeholder="Email Address" required/>
                              <input type="text" name="subject" id="formSubject" className="input" placeholder="Subject" required/>
                          </div>
                          <div className="msg">
                              <textarea type="text" name="message" id="formMessage" placeholder="Message" required></textarea>
                          <button type="submit" className="btn">send</button>
                          </div>
                      </div>
                  </div>
              </div>
        </form>
      </Col>
    </Row>

  </Container>
  )
}
